body{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    overflow: hidden;
}
.MainWrapper{
    position: absolute;
    left:12%;
    top:80px;
    width:88%;
    height:100%;
    background-color:#efeff6;
}

@media only screen and (max-width: 1350px) {
    .MainWrapper{width:85%;left:15%;}
}
body{
    margin: 0 auto;
    background-color: white;
    background-repeat: no-repeat;
}

.container{
    width: 500px;
    height: 400px;
    text-align: center;
    background-color: rgb(0, 51, 153, .5);
    border-radius: 4px;
    margin: 0 auto;
    margin-top: 150px;
}

.container img{
    width: 50%;
    height: 25%;
    margin-top: -60px;
    margin-bottom: 30px;
}

input{
    height: 50px;
    width: auto;
    font-size: 18px;
    border: none;
    margin-bottom: 20px;
    background-color: #ffffff;
    padding-left: 40px;
}

.form-input::before{
    content: "\F007";
    position: absolute;
    font-family: "FontAwesome";
    padding-left: 5px;
    color: rgb(0, 0, 0, .2);
    padding-top: 7px;
    font-size: 35px;
}



.form-input:nth-child(2)::before{
    content: "\F023";
}

.btn-login{
    padding: 15px 25px;
    cursor: pointer;
    color: #fff;
    border-radius: 4px;
    border: none;
    background-color: rgb(0, 51, 153);
    border-bottom: 4px solid #002b80;
}
.navbar{
    position: absolute;
    width:12%;
    height:100%;
    padding:0px;
}

@media only screen and (max-width: 1350px) {
    .navbar{width: 15%;}
}

.navbar-nav{
    position: absolute;
    top:100px;

}

.nav-item{
    margin-top:30px;
    margin-bottom:30px;
    cursor: pointer;
    height:40px;
}

.nav-link{
    color:white;
    margin-left:35px;
}

.nav-item:hover + .listIcon{
    color:rgba(255,255,255,1);
}

.sperator{
    position: absolute;
    top:80px;
    width: 100%;
    height:1px;
    left:0px;
    background-color: gray;
}

.listIcon, .plusIcon, .fileIcon{
    position: relative;
    left:10px;
    top:40px;
    color:rgba(255,255,255,.5);
}

.userIcon{
    position: absolute;
    top:20px;
    left:20px;
    font-size:40px;
    color:rgba(255,255,255,.5);
}

.logoutIcon{
    position: absolute;
    bottom:20px;
    left:20px;
    font-size: 30px;
    color:rgba(255,255,255,.5);
    cursor: pointer;
}

.logoutWrapper:hover .logoutIcon, .logoutWrapper:hover .logoutTag{
    color:rgba(255,255,255,1);
}

.logoutTag{
    position: absolute;
    bottom:20px;
    left:60px;
    color:rgba(255,255,255,.5);
    cursor: pointer;
}

.username{
    position: absolute;
    top:30px;
    left:70px;
    color:rgba(255,255,255,.5);

}

.Wrapper{
    cursor: pointer;
}

.Wrapper:hover .userIcon{
    color:rgba(255,255,255,1);
}

.Wrapper:hover .username{
    color:rgba(255,255,255,1);
}

.underline{
    position: relative;
    left:5px;
    top:0px;
    width:0;
    height:1px;
    background-color: white;
    -webkit-transition:0.4s;
    transition:0.4s;
}

.nav-item:hover .underline{
    width:100%;
}

.nav-item:hover .listIcon,.nav-item:hover .plusIcon,.nav-item:hover .fileIcon{
    color:rgba(255,255,255,1);
}
.SearchBar{
    position: absolute;
    top:0px;
    left:200px;
    height:80px;
    width:100%;
}

.HKTLogo{
    position: absolute;
    top:15px;
    right:225px;
}

.searchIcon{
    position: absolute;
    top:25px;
    left: 400px;
    font-size: 25px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.textBar{
    position: absolute;
    top:15px;
    left:250px;
    height:50px;
    width:150px;
    border-bottom:1px solid grey;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    color:rgba(172, 172, 172, 0.5);
    padding:0px;
}

.textBar:focus{
    outline: none;
    width:350px;
    color:black;
}

.textBar:not(.textBar:focus){
    content:'';
}

.textBar:focus + i{
    left:600px;
}

.selectOrderFields{
    position: absolute;
    top:30px;
    left:50px;

}
.orderTable{
    position: absolute;
    top:90px;
    text-align: center;
    width:100%;
}



.heading{
    position: absolute;
    top:20px;
    left:20px;
}



.UploadPage{
    position: absolute;
    left:200px;
    top:80px;
    width:100%;
    height:100%;
    background-color:#efeff6;
}
